import InputArea from "../../plugin/components/InputArea";
export default {
  name: "userList",
  components: {
    InputArea
  },
  data() {
    return {
      currentPage: 1,
      pagesize: 5,
      total: 0,
      dialogAddRole: false,
      dialogTitle: "",
      formLabelWidth: "120px",
      roleId: -1, //权限分配id
      form: {
        role_id: -1,
        name: "",
        display_name: ""
      },
      form2: {
        role: ""
      },
      dialogRoleAssign: false,
      dialogRightAssign: false,
      tableData: [],
      multipleSelection: [],
      rightData: [],
      defaultProps: {
        children: "children",
        label: "display_name"
      }
    };
  },

  methods: {
    async getRoleList() {
      const res = await this.$http.get("api/auth/roles", {
        params: {
          page: this.currentPage,
          limit: this.pagesize
        }
      });
      console.log(res);
      if (res.data.code === 200) {
        this.tableData = res.data.data;
        this.total = res.data.data.total;
        this.$message({
          type: "success",
          message: res.data.msg
        });
      } else {
        this.$message({
          type: "info",
          message: res.data.msg
        });
      }
    },
    async getRigthList() {
      const res = await this.$http.get("api/auth/permissions");
      console.log(res);
    },
    async addRoleFn() {
      if (!this.form.role_id) {
        const res = await this.$http.post("api/auth/roles", {
          name: this.form.name,
          display_name: this.form.display_name
        });
        if (res.data.code === 200) {
          this.dialogAddRole = false;
          this.getRoleList();
          this.$message({
            type: "success",
            message: res.data.msg
          });
        } else {
          this.$message({
            type: "info",
            message: res.data.msg
          });
        }
      } else {
        console.log(this.form);
        const res = await this.$http.put("api/auth/roles", {
          name: this.form.name,
          display_name: this.form.display_name,
          role_id: this.form.role_id
        });
        console.log(res)
        if (res.data.code === 200) {
          this.dialogAddRole = false;
          this.getRoleList();
          this.$message({
            type: "success",
            message: res.data.msg
          });
        } else {
          this.$message({
            type: "info",
            message: res.data.msg
          });
        }
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log(this.multipleSelection);
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pagesize = val;
      this.getRoleList();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getRoleList();
    },
    addRole() {
      this.dialogAddRole = true;
      this.dialogTitle = "添加角色";
      this.form = {
        name: "",
        diaplay_name: ""
      };
    },
    handleEdit(index, row) {
      console.log(index, row);
      this.dialogAddRole = true;
      this.dialogTitle = "编辑角色";
      this.form.name = row.name;
      this.form.display_name = row.display_name;
      this.form.role_id = row.id;
    },
    // async editRoleFn() {
    //   console.log('woshi')
    //   const res = await this.$http.put('auth/roles',{
    //     username: this.form.name,
    //     display_name: this.form.display_name
    //   })
    //   console.log(res)
    // },
    handleRole(index, row) {
      this.dialogRoleAssign = true;
    },
    async handleRight(index, row) {
      this.dialogRightAssign = true;
      this.roleId = row.id;
      const res = await this.$http.get("api/auth/permissions");
      console.log(res);
      if (res.data.code === 200) {
        this.rightData = res.data.data;
      }
    },
    handleDelete(index, row) {
      this.form.role_id = row.id;
      console.log(this.form.role_id);
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          const res = await this.$http.delete("api/auth/roles", {
            params: {
              role_id: this.form.role_id
            }
          });
          if (res.data.code === 200) {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            this.getRoleList();
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  },
  mounted() {
    this.getRoleList();
    this.getRigthList();
  }
};



